import React from "react";
import { Grid } from "@mui/material";
import { makeStyles } from "@mui/styles";

import LogoJava from "../../../images/LogoJava.png";
import LogoJavaScript from "../../../images/LogoJavaScript.png";
import LogoPython from "../../../images/LogoJavapython.png";

import StorageIcon from "@mui/icons-material/Storage";
import CodeIcon from "@mui/icons-material/Code";

//CSS
import "./estilos/Home.css";

const useStyle = makeStyles({
  box: {
    height: "50vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  boxMid: {
    height: "50vh",
    paddingTop: "6%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  boxMain: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
    height: "50vh",
  },
  // icons: {
  //   display: "flex",
  //   justifyContent: "center",
  //   alignItems: "center",
  //   backgroundColor: "#3700B3",
  //   borderRadius: "5px",
  //   padding: "2% 0%",
  //   border: "1px solid white",
  //   margin: "15% 30%",
  // },
  title: {
    //font
    fontFamily: "Gill Sans, sans-serif",
    color: "white",
    textAlign: "center",
    // fontSize: "100%",
    // margin: "3% 50%"
  },
  // subTitle: {
  //   //font
  //   fontFamily: "Gill Sans, sans-serif",
  //   color: "white",
  //   textAlign: "center",
  //   borderBlockEnd: "1px solid white",
  //   marginBottom: "2px",
  // },
  // body: {
  //   //font
  //   fontFamily: "Gill Sans, sans-serif",
  //   color: "white",
  //   textAlign: "center",
  //   fontSize: "2rem",
  // },
  textDiv: {
    display: "flex",
    justifyContent: "center",
    padding: "2% 0%",
    textAlign: "center",
  },
  icone: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
});

const PageCarousel = (props) => {
  const classes = useStyle();
  return (
    <Grid
      className={classes.boxMain}
      container
      spacing={0}
      columns={{ xs: 12, sm: 12, md: 12 }}
    >
      <Grid className={classes.box} item xs={4} md={4}>
        {props.page === 1 ? (
          <div className="images">
            <img src={LogoJavaScript} alt="" />
          </div>
        ) : props.page === 2 ? (
          <div className="icons">
            <Grid container spacing={0}>
              <Grid item xs={12} sm={12} md={12}>
                <StorageIcon
                  className={classes.icone}
                  sx={{ width: "70%", color: "white", height: "auto" }}
                />
              </Grid>
              <Grid item xs={12} sm={12} md={12}>
                <h5 className="subTitleCard">BACK-END</h5>
              </Grid>
            </Grid>
          </div>
        ) : (
          <div className={classes.textDiv}>
            <Grid container spacing={0}>
              <Grid item xs={12} sm={12} md={12}>
                <div className="titleTextDesc" variant="h6" component="div">
                  O QUE FAO?
                </div>
                <div className="bodyDesc" variant="body1" component="div">
                  <p>
                    Um Cientista da Computao em fase de desenvolvimento pela
                    PUC Minas, Administrador pelo SENAI e um entusiasta por
                    novas tecnologia. Acredito que curiosidade e persistncia 
                    o que me define. Busco sempre colocar a mo na massa, me
                    colocar a frente e levar minha equipe comigo. Me considero
                    bastante comunicativo e criativo.
                  </p>
                  <p>
                    Sempre me interessei por inovao e tecnologia, fui diretor
                    de Marketing de uma miniempresa de nome NotePort em 2018,
                    mediado pela Junior Achievement, uma das maiores
                    organizaes sociais incentivadoras de jovens do mundo.
                  </p>
                </div>
              </Grid>
            </Grid>
          </div>
        )}
      </Grid>
      <Grid className={classes.boxMid} item xs={3} md={3}>
        {props.page === 1 ? (
          <div className="images">
            <img src={LogoJava} alt=""/>
          </div>
        ) : props.page === 2 ? (
          <div className={classes.textDiv}>
            <Grid container spacing={0}>
              <Grid item xs={12} sm={12} md={12}>
                <div className="titleText" variant="h6" component="div">
                  O QUE FAO?
                </div>
                <div className="subTitle" variant="subtitle1" component="div">
                  Um pouco de tudo...
                </div>
                <div className="body" variant="body1" component="div">
                  Sempre peguei diferentes projetos que exigiam diferentes
                  competncias, me tornando assim implicitamente um programador
                  Full Stack.
                </div>
              </Grid>
            </Grid>
          </div>
        ) : (
          <div className="designBox" ></div>
        )}
      </Grid>
      <Grid className={classes.box} item xs={4} md={4}>
        {props.page === 1 ? (
          <div className="images">
            <img src={LogoPython} alt=""/>
          </div>
        ) : props.page === 2 ? (
          <div className="icons">
            <Grid container spacing={0}>
              <Grid item xs={12} sm={12} md={12}>
                <CodeIcon
                  className={classes.icone}
                  sx={{ width: "70%", color: "white", height: "auto" }}
                />
              </Grid>
              <Grid item xs={12} sm={12} md={12}>
                <h5 className="subTitleCard">FRONT-END</h5>
              </Grid>
            </Grid>
          </div>
        ) : (
          <div className="bodyDesc" variant="body1" component="div">
            <p>
              Hoje, como estagirio, trabalho como desenvolvedor Full Stack na
              Pattrol. Tenho experincia em desenvolvimento de aplicativos
              android e aplicaes Webs, alm de criao de APIs de ASP.NET e
              desenvolvimento de Banco de Dados SQL.
            </p>
            <p>
              A disciplina  o pilar para a perfeio. Tudo que se faz repetidas
              vezes e com excelncia, se torna algo absoluto e pleno. 
            </p>
          </div>
        )}
      </Grid>
    </Grid>
  );
};

export default PageCarousel;
