//importar funes
import React, { useState } from "react";
import { makeStyles } from "@mui/styles";

//CSS
import "./estilos/contact.css";

//importar componentes
import {
  Grid,
  Typography,
  Box,
  Snackbar,
  IconButton,
  Link,
} from "@mui/material";

//importar cones
import PersonIcon from "@mui/icons-material/Person";
import CloseIcon from "@mui/icons-material/Close";

//importar imagens
import gmail from "../../../images/gmail.png";
import linkedin from "../../../images/linkedin.png";
import telefone from "../../../images/telefone.png";

const useStyle = makeStyles({
  imagem: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",

    height: "100%",
    fontSize: "300%",
    zIndex: "-4",
  },
  title: {
    opacity: "1",
    color: "white",
  },
  icons: {
    maxWidth: "100%",
    maxHeight: "100%",
  },
});

const Contact = () => {
  //Definir classes
  const classes = useStyle();

  const [openSnackBar, setOpenSnackBar] = useState(false);

  return (
    <div className="mainContact">
      <Box>
        <div className="banner">
          <div className={classes.imagem}>
            <div className={classes.title}>
              <Typography variant="h2" className={classes.title}>
                CONTACT
              </Typography>
            </div>
          </div>
        </div>
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          alignItems: "center",
          height: "40vh",
        }}
      >
        <Grid container>
          <Grid
            item
            sm={12}
            md={4}
            sx={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <div
              className="img"
              onClick={() => {
                navigator.clipboard.writeText("matheusaguillarpego@gmail.com");
                setOpenSnackBar(true);
              }}
            >
              <Link href="#">
                <img src={gmail} className={classes.icons} alt=""/>
              </Link>
            </div>
          </Grid>
          <Grid
            item
            sm={12}
            md={4}
            sx={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <div className="img">
              <Link href="https://www.linkedin.com/in/matheus-aguilar-pego-1524031a2" target="_blank">
                <img src={linkedin} className={classes.icons} alt=""/>
              </Link>
            </div>
          </Grid>
          <Grid
            item
            sm={12}
            md={4}
            sx={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <div
              className="img"
              onClick={() => {
                navigator.clipboard.writeText("31995380347");
                setOpenSnackBar(true);
              }}
            >
              <Link href="#">
                <img src={telefone} className={classes.icons} alt=""/>
              </Link>
            </div>
          </Grid>
        </Grid>
      </Box>
      <Box
        sx={{
          backgroundColor: "#6200EE",
          height: "10vh",
        }}
      >
        <Snackbar
          open={openSnackBar}
          message={"Copiado com sucesso"}
          action={
            <IconButton
              size="small"
              aria-label="close"
              color="inherit"
              onClick={() => {
                setOpenSnackBar(false);
              }}
            >
              <CloseIcon fontSize="small" />
            </IconButton>
          }
        />

        <Grid container>
          <Grid
            item
            sm={6}
            sx={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "end",
              alignItens: "center",

              paddingRight: "3%",
            }}
          >
            <PersonIcon sx={{ color: "white", fontSize: "250%" }} />
          </Grid>
          <Grid
            item
            sm={6}
            sx={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "start",
              alignItens: "center",

              fontFamily: "Gill Sans, sans-serif",
              color: "white",
              textAlign: "center",
              fontWeight: "bold",
            }}
          >
            <p>MATHEUS AGUILAR</p>
          </Grid>
        </Grid>
      </Box>
    </div>
  );
};

export default Contact;
