import React, { useState } from "react";
import { makeStyles } from "@mui/styles";
import { Grid } from "@mui/material";

import LogoLinguagens from "../../../images/LogosLinguagens.png";
import LogoFrameWorks from "../../../images/FrameWorkLogos.png";

import DeveloperBoardIcon from "@mui/icons-material/DeveloperBoard";
import PersonAddAlt1Icon from "@mui/icons-material/PersonAddAlt1";

import "./estilos/Skills.css";

const stylePage = makeStyles({
  body: {
    color: "white",
    fontFamily: "Gill Sans, sans-serif",
    textAlign: "center",

    position: "fixed",
    backgroundColor: "#0C001F",
    height: "100vh",
    width: "100%",
  },
  block: {
    //Tela
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    minHeight: "100%",

    padding: "5%",
  },
  blockImage: {
    //Tela
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    minHeight: "100%",

    padding: "5%",
  },
  imagem: {
    maxWidth: "100%",
    maxHeight: "100%",
  },
  subBlock: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    lineHeight: "170%",

    textAlign: "justify",
    minWidth: "100%",
    minHeight: "100%",
  },
  title: {
    //font
    fontFamily: "Gill Sans, sans-serif",
    color: "white",
    textAlign: "center",

    marginBottom: "20%",
  },
  contentTxt: {
    float: "left",
    width: "100%",
  },
  teste: {
    position: "absolute",
  },
});

const BlockContent = (props) => {
  //Definir classes
  const classes = stylePage();

  const [numberImg, setNumberImg] = useState(1);
  const [numberTxt, setNumberTxt] = useState(1);

  const [startScroll, setStartScroll] = useState(true);

  return (
    <div
      className={classes.body}
      id="primaryBlock"
      onWheel={(event) => {
        if (startScroll) {
          //Pegar elemento para controle de animao

          const elementImagem = document.getElementById("imagemId" + numberImg);
          const computedStyleImg = window.getComputedStyle(elementImagem);

          const elementText = document.getElementById("txtId" + numberTxt);
          const computedStyleTxt = window.getComputedStyle(elementText);

          //Verificar se a animao deve ir para frente ou para trs
          if (event.deltaY / 100 > 0) {
            //Mudar classe do elemento para classe j animada

            if (computedStyleImg.getPropertyValue("margin-left") === "0px") {
              //Classe que faz a imagem desaparecer
              elementImagem.className = "imgFade";
            } else {
              //Classe que move a imagem
              elementImagem.className = "imgRun";
            }

            if (computedStyleTxt.getPropertyValue("margin-right") === "0px") {
              elementText.className = "txtFade";
            } else {
              elementText.className = "txtRun";
            }

            //Rodar animao de acordo com o scroll do mouse

            elementImagem.style.animationPlayState = "running";
            elementText.style.animationPlayState = "running";

            setStartScroll(false);

            setTimeout(() => {
              elementImagem.style.animationPlayState = "paused";
              elementText.style.animationPlayState = "paused";

            //Atualizar dados
            //texto
            document.documentElement.style.setProperty(
              "--my-start-txtRunReverse",
              computedStyleTxt.getPropertyValue("margin-right")
            );
            document.documentElement.style.setProperty(
              "--my-start-txtRun",
              computedStyleTxt.getPropertyValue("margin-right")
            );

            //imagem
            document.documentElement.style.setProperty(
              "--my-start-imgRun",
              computedStyleImg.getPropertyValue("margin-left")
            );

            document.documentElement.style.setProperty(
              "--my-start-imgRunReverse",
              computedStyleImg.getPropertyValue("margin-left")
            );

            //Fade Imagem
            document.documentElement.style.setProperty(
              "--my-start-imgFade",
              computedStyleImg.getPropertyValue("opacity")
            );

            document.documentElement.style.setProperty(
              "--my-start-imgFadeReverse",
              computedStyleImg.getPropertyValue("opacity")
            );

            //Fade texto
            document.documentElement.style.setProperty(
              "--my-start-txtFade",
              computedStyleImg.getPropertyValue("opacity")
            );

            document.documentElement.style.setProperty(
              "--my-start-txtFadeReverse",
              computedStyleImg.getPropertyValue("opacity")
            );

            if (computedStyleTxt.getPropertyValue("opacity") === "0") {
              //Restaurando posio inicial da classe
              document.documentElement.style.setProperty(
                "--my-start-txtRun",
                "500%"
              );

              //Passando para a proxima imagem
              if (numberTxt < 4) {
                let number = numberTxt;
                setNumberTxt(++number);
              }
            }

            if (computedStyleImg.getPropertyValue("opacity") === "0") {
              //Restaurando posio inicial da classe
              document.documentElement.style.setProperty(
                "--my-start-imgRun",
                "500%"
              );

              //Passando para a proxima imagem
              if (numberImg < 4) {
                let number = numberImg;
                setNumberImg(++number);
              }
            }

            setStartScroll(true);
          }, 500);
          }

          if (event.deltaY / 100 < 0) {
            //Mudar classe do elemento para classe j animada
            if (computedStyleImg.getPropertyValue("opacity") === "1") {
              elementImagem.className = "imgRunReverse";
            } else {
              elementImagem.className = "imgFadeReverse";
            }

            if (computedStyleTxt.getPropertyValue("opacity") === "1") {
              elementText.className = "txtRunReverse";
            } else {
              elementText.className = "txtFadeReverse";
            }

            //Rodar animao de acordo com o scroll do mouse
            elementImagem.style.animationPlayState = "running";
            elementText.style.animationPlayState = "running";
            setStartScroll(false);

            setTimeout(() => {
              elementImagem.style.animationPlayState = "paused";
              elementText.style.animationPlayState = "paused";

              //Atualizar dados
              //texto
              document.documentElement.style.setProperty(
                "--my-start-txtRun",
                computedStyleTxt.getPropertyValue("margin-right")
              );
              document.documentElement.style.setProperty(
                "--my-start-txtRunReverse",
                computedStyleTxt.getPropertyValue("margin-right")
              );

              //imagem
              document.documentElement.style.setProperty(
                "--my-start-imgRun",
                computedStyleImg.getPropertyValue("margin-left")
              );

              document.documentElement.style.setProperty(
                "--my-start-imgRunReverse",
                computedStyleImg.getPropertyValue("margin-left")
              );

              //Fade texto
              document.documentElement.style.setProperty(
                "--my-start-imgFade",
                computedStyleImg.getPropertyValue("opacity")
              );

              document.documentElement.style.setProperty(
                "--my-start-imgFadeReverse",
                computedStyleImg.getPropertyValue("opacity")
              );

              //Fade Imagem
              document.documentElement.style.setProperty(
                "--my-start-imgFade",
                computedStyleImg.getPropertyValue("opacity")
              );

              document.documentElement.style.setProperty(
                "--my-start-imgFadeReverse",
                computedStyleImg.getPropertyValue("opacity")
              );

              //Calcular a porcetagem da margin para imagem
              const marginLeftInPx = computedStyleImg.marginLeft;
              const parentElement = elementImagem.parentElement;
              const parentWidth = parentElement.offsetWidth;
              const marginLeftInPercentage =
                (parseFloat(marginLeftInPx) / parentWidth) * 100;

              const marginLeftInPxTxt = computedStyleTxt.marginRight;
              const parentElementTxt = elementText.parentElement;
              const parentWidthTxt = parentElementTxt.offsetWidth;
              const marginLeftInPercentageTxt =
                (parseFloat(marginLeftInPxTxt) / parentWidthTxt) * 100;

                if (
                  marginLeftInPercentage > 100 &&
                  marginLeftInPercentageTxt > 100
                  ) {

                //Restaurando posio inicial da classe
                document.documentElement.style.setProperty(
                  "--my-start-imgRunReverse",
                  "0%"
                );
                document.documentElement.style.setProperty(
                  "--my-start-imgFadeReverse",
                  "0%"
                );

                //Passando para a proxima imagem
                if (numberImg > 1) {
                  let number = numberImg;
                  setNumberImg(--number);
                }
                //Restaurando posio inicial da classe
                document.documentElement.style.setProperty(
                  "--my-start-txtRunReverse",
                  "0%"
                );
                document.documentElement.style.setProperty(
                  "--my-start-txtFadeReverse",
                  "0%"
                );

                //Passando para a proximo texto
                if (numberTxt > 1) {
                  let number = numberTxt;
                  setNumberTxt(--number);
                }
              }

              setStartScroll(true);
            }, 500);
          }
        }
      }}
    >
      {/* <div id="teste">TESTE</div> */}

      <Grid container spacing={0}>
        <Grid item xs={5} className={classes.block}>
          <div className={classes.subBlock}>
            <div id="txtId4">
              <Grid container>
                <Grid item xs={12} className={classes.title}>
                  <h2>Adicionais</h2>
                </Grid>
                <Grid item xs={12}>
                  <ul>
                    <li>Pacote Office</li>
                    <li>Photoshop intermedirio</li>
                    <li>Conhecimento em edio de vdeo (Sony Vegas)</li>
                    <li>Noes de marketing e design</li>
                  </ul>
                </Grid>
              </Grid>
            </div>
            <div id="txtId3">
              <Grid container>
                <Grid item xs={12} className={classes.title}>
                  <h2>Hardware</h2>
                </Grid>
                <Grid item xs={12}>
                  <p className={classes.contentTxt}>
                    Alm de do Software, aprendi bastante sobre o funcionamento
                    de cada componente constituinte de um computador. No s
                    isso, mas sei montar mquinas, identificar problemas
                    relacionadas ao correto funcionamento do Hardware e
                    resolve-los.
                  </p>
                </Grid>
              </Grid>
            </div>
            <div id="txtId2">
              <Grid container>
                <Grid item xs={12} className={classes.title}>
                  <h2>Frameworks</h2>
                </Grid>
                <Grid item xs={12}>
                  <p className={classes.contentTxt}>
                    No s de linguagens vive um programador. Alm dessas
                    linguagens aprendi o funcionamento de alguns frameworks
                    muito importantes, principalmente para o desenvolvimento
                    Web, como: React, Flutter, jQuery, Bootstrap. Um bom exemplo
                    dessa do uso disso,  esse site, que foi inteiramente
                    desenvolvido por React.
                  </p>
                </Grid>
              </Grid>
            </div>
            <div id="txtId1">
              <Grid container>
                <Grid item xs={12} className={classes.title}>
                  <h2>Linguagens de Programao</h2>
                </Grid>
                <Grid item xs={12}>
                  <p className={classes.contentTxt}>
                    Ao longo da minha jornada acadmica consegui acumular algum
                    conhecimento em algumas linguagens especficas, tais como:
                    Java, C++, Javascript, SQL e Kotlin, todas essas a nvel
                    intermedirio. J outras, apesar de ter um pouco menos de
                    experiencia, tive um contato legal com elas, tanto dentro da
                    faculdade ou at mesmo durante projetos de sistemas
                    profissionais, tais como: C#, Python e Dart.
                  </p>
                </Grid>
              </Grid>
            </div>
          </div>
        </Grid>
        <Grid item xs={7} className={classes.blockImage}>
          <div id="imagemId4">
            <PersonAddAlt1Icon
              className={classes.imagem}
              sx={{ fontSize: 300 }}
            />
          </div>
          <div id="imagemId3">
            <DeveloperBoardIcon
              className={classes.imagem}
              sx={{ fontSize: 300 }}
            />
          </div>
          <div id="imagemId2">
            <img src={LogoFrameWorks} alt="" className={classes.imagem} />
          </div>

          <div id="imagemId1">
            <img src={LogoLinguagens} alt="" className={classes.imagem} />
          </div>
        </Grid>
      </Grid>
      {/* <div className="teste">
        teste
      </div> */}
    </div>
  );
};

export default BlockContent;
