import React from "react";
import Banner from "../../../images/banner3.jpg";
import { makeStyles } from "@mui/styles";
import PageCarousel from "./PageCarousel";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";

//CSS
import "./estilos/Home.css";

const stylePage = makeStyles({
  banner: {
    minHeight: "80%",
    width: "100%",
    maxHeight: "100vh",
  },

  divDivider: {
    minWidth: "80%",
    width: "100%",
    height: "50vh",
    backgroundAttachment: "local scroll",
    background: `url(${Banner}) repeat fixed center`,
    backgroundSize: "cover",
    borderBottom: "2px white solid",
  },
  divCarrousel: {
    backgroundColor: "#0C001F",
    maxWidth: "100%",
    MaxHeight: "50vh",
  },
  divAux: {
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "row",
    display: "flex",
  },
  carousel: {
    MaxHeight: "50vh",
    padding: "0px",
  },
  boxArrowLeft: {
    right: "0%",

    // position: "absolute",
    zIndex: "10",
    // top: "30vh",
  },
  boxArrowRight: {
    // right: "px",
    backgroundColor: "green",

    // position: "absolute",
    zIndex: "5",
    // bottom: "29vh",
    // left: "95%",
  },
  iconBoxArrowRight: {
    position: "fixed",
    right: "100vw",
    zIndex: "5",
    padding: "0%",
  },
});

const MainFeed = () => {
  const classes = stylePage();

  return (
    <div className="main">
      <div className={classes.divDivider} />
      <div className={classes.divAux}>
        <div className="fotoPerfil" />
      </div>
      <div className={classes.divCarrousel}>
        <Carousel
          className={classes.carousel}
          showArrows={true}
          autoPlay={true}
          dynamicHeight={false}
          width={"100%"}
          showThumbs={false}
          infiniteLoop={true}
        >
          <div>
            <PageCarousel page={1} />
          </div>
          <div>
            <PageCarousel page={2} />
          </div>
          <div>
            <PageCarousel page={3} />
          </div>
        </Carousel>
      </div>
    </div>
  );
};

export default MainFeed;
