//Importar funes
import React, { useState } from "react";
import { makeStyles } from "@mui/styles";

//CSS
import "./estilos/Experience.css";

//importar imagens
import pattrol from "../../../images/experience/pattrol.png";
import consultoriar from "../../../images/experience/consultoriar.png";

//importar componentes
import { Box, Grid } from "@mui/material";

const pageStyle = makeStyles({
  teste: {
    backgroundColor: "black",
    height: "100vh",
    width: "100%",
    // position: "inherit",
  },
  img: {
    height: "100vh",
    width: "auto",

    backgroundSize: "cover",
    backgroundImage: `URL(${pattrol})`,
    marginLeft: "50%",
    // backgroundAttachment: "local scroll",
  },
  title: {
    marginTop: "4%",
  },
  title1: {
    marginTop: "5%",
  },
  bodyText: {
    margin: "8% 0",
  },
});

const Experience = () => {
  //Definir classes
  const classes = pageStyle();

  const [imgNumber, setImgNumber] = useState(1);
  const [txtNumber, setTxtNumber] = useState(1);

  const [startScroll, setStartScroll] = useState(true);

  return (
    <div className="mainXp">
      <Box
        sx={{}}
        onWheel={(event) => {
          if (startScroll) {
            //Obter propriedades do elemento
            const elementImg = document.getElementById("img" + imgNumber);
            const styleImg = window.getComputedStyle(elementImg);

            const elementTxt = document.getElementById("txt" + txtNumber);
            const styleTxt = window.getComputedStyle(elementTxt);

            if (event.deltaY / 100 > 0) {
              //mudar para classe animada
              elementImg.className = "imgRunXp";

              if (txtNumber > 1) {
                const elementTxtAnt = document.getElementById(
                  "txt" + (txtNumber - 1)
                );
                const elementImgAnt = document.getElementById(
                  "img" + (imgNumber - 1)
                );
                elementImgAnt.style.zIndex = "0";
                elementTxtAnt.style.zIndex = "0";

                elementImg.style.zIndex = "1";
                elementTxt.style.zIndex = "1";
              }

              elementTxt.className = "txtRunXp";

              //comear a animao
              elementImg.style.animationPlayState = "running";
              elementTxt.style.animationPlayState = "running";

              setStartScroll(false);

              setTimeout(() => {
                elementImg.style.animationPlayState = "paused";
                elementTxt.style.animationPlayState = "paused";

                setStartScroll(true);

                //Atualizar dados
                //texto
                document.documentElement.style.setProperty(
                  "--my-start-imgRunReverseXp",
                  styleImg.getPropertyValue("margin-right")
                );
                document.documentElement.style.setProperty(
                  "--my-start-txtRunReverseXp",
                  styleTxt.getPropertyValue("margin-left")
                );

                //Passando para a proximo elemento
                if (imgNumber < 2) {
                  let number = imgNumber;
                  setImgNumber(++number);
                }

                if (txtNumber < 2) {
                  let number = txtNumber;
                  setTxtNumber(++number);
                }
              }, 1500);
            }

            if (event.deltaY / 100 < 0) {
              //mudar para classe animada
              elementImg.className = "imgRunXpReverse";

              elementTxt.className = "txtRunXpReverse";

              //comear a animao
              elementImg.style.animationPlayState = "running";
              elementTxt.style.animationPlayState = "running";

              setStartScroll(false);

              setTimeout(() => {
                elementImg.style.animationPlayState = "paused";
                elementTxt.style.animationPlayState = "paused";

                setStartScroll(true);

                //Atualizar dados
                //texto
                document.documentElement.style.setProperty(
                  "--my-start-imgRunXp",
                  styleImg.getPropertyValue("margin-right")
                );
                document.documentElement.style.setProperty(
                  "--my-start-txtRunXp",
                  styleTxt.getPropertyValue("margin-left")
                );

                //Restaurando posio inicial da classe
                document.documentElement.style.setProperty(
                  "--my-start-imgRunReverseXp",
                  "0%"
                );

                document.documentElement.style.setProperty(
                  "--my-start-txtRunReverseXp",
                  "0%"
                );

                //Passando para a proximo elemento

                if (txtNumber > 1) {
                  const elementImgAnt = document.getElementById(
                    "txt" + (txtNumber - 1)
                  );
                  elementImgAnt.style.opacity = "1";
                }

                if (imgNumber > 1) {
                  let number = imgNumber;
                  setImgNumber(--number);
                }

                if (txtNumber > 1) {
                  let number = txtNumber;
                  setTxtNumber(--number);
                }
              }, 1500);
            }
          }
        }}
      >
        <div id="blockImg">
          <img src={pattrol} id="img1" alt=""/>
          <img src={consultoriar} id="img2" alt=""/>
        </div>

        <div id="txt2">
          <Grid
            container
            spacing={0}
            sx={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "center",

              height: "100vh",
              minWidth: "100%",

              padding: "0 10%",

              opacity: "0.8",
              // backgroundColor: "black",
            }}
          >
            <Grid
              item
              sm={12}
              sx={{
                fontFamily: "Gill Sans, sans-serif",
                textAlign: "center",
                fontWeight: "bold",
                color: "white",
                fontSize: "130%",
              }}
            >
              <h1 className={classes.title1}>CONSULTORIAR</h1>

              <h4 className={classes.title}>Estagirio</h4>

              <p className={classes.bodyText}>
                Suporte a empresas externas; Resoluo de problemas de hardware e
                software de computadores empresariais e pessoais; Atendimento a
                clientes via TeamViewer.
              </p>
            </Grid>
          </Grid>
        </div>

        <div id="txt1">
          <Grid
            container
            spacing={0}
            sx={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "center",

              height: "100vh",
              minWidth: "100%",

              padding: "0 10%",

              opacity: "0.8",
              // backgroundColor: "black",
            }}
          >
            <Grid
              item
              sm={12}
              sx={{
                fontFamily: "Gill Sans, sans-serif",
                textAlign: "center",
                fontWeight: "bold",
                color: "white",
                fontSize: "130%",
              }}
            >
              <h1 className={classes.title1}>PATTROL</h1>

              <h4 className={classes.title}>Estagirio</h4>

              <p className={classes.bodyText}>
                Desenvolvimento de Banco de Dados SQL na plataforma Azure
                (Microsoft), desenvolvimento de API de ASP.NET para Web em C#,
                aplicaes utilizando React.js, desenvolvimento de aplicativo
                Android usando a linguagem Kotlin atravs da plataforma Android
                Studio, criao de um sistema de manuteno preventiva em Python
                usando a biblioteca Pandas
              </p>
            </Grid>
          </Grid>
        </div>
      </Box>
    </div>
  );
};

export default Experience;
