//importar funes
import React, { useState } from "react";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { TabContext, TabPanel } from "@mui/lab";
import { makeStyles } from "@mui/styles";

//importar componentes
import { Box, Tab, Tabs } from "@mui/material";

import Home from "../body/home/MainFeed";
import Skills from "../body/skills/Skills";
import Contact from "../body/contact/Contact";
import Experience from "../body/experience/Experience";

const theme = createTheme({
  palette: {
    neutral: {
      main: "#000",
      contrastText: "#ffffff",
    },
    primary: {
      main: "#6200EE",
      contrastText: "#ffffff",
    },
    secondary: {
      main: "#ffffff",
      contrastText: "#ffffff",
    },
    text: {
      primary: "#6200EE",
      secondary: "#ffffff",
    },
  },
});

const stylePage = makeStyles({
  bodyOfHeader: {
    width: "100%",
    position: "sticky",
    top: "0",
    left: "0",
  },
  headerbox: {
    //colors
    backgroundColor: "#6200EE",

    //font
    fontFamily: "Roboto",

    //position
    padding: "0.7%",
  },
  styleDiv: {
    color: "#black",
  },
  componentsDiv: {
    
  },
});

const Header = () => {
  const classes = stylePage();
  const [value, setValue] = useState("home");

  const handleChange = (event, newValue) => {
    // console.log(document.body.style.overflow);
    // document.body.style.overflow = "hidden";
    // if(document.body.style.backgroundColor == "blue"){
    //   document.body.style.backgroundColor = "red";
    // }else{
    //   document.body.style.backgroundColor = "blue";
    // }
    setValue(newValue);
  };

  return (
    <div className={classes.styleDiv}>
      <ThemeProvider theme={theme}>
        <TabContext value={value}>
          <div className={classes.bodyOfHeader}>
            <Box sx={{ width: "100%" }}>
              <Tabs
                className={classes.headerbox}
                value={value}
                onChange={handleChange}
                textColor="secondary"
                indicatorColor="secondary"
                aria-label="secondary tabs example"
                centered
              >
                <Tab
                  sx={{
                    color: "text.secondary",
                    padding: "0% 2%",
                  }}
                  value="home"
                  label="HOME"
                />

                <Tab
                  sx={{
                    color: "text.secondary",
                    padding: "0% 2%",
                  }}
                  value="skills"
                  label="SKILLS"
                />
                <Tab
                  sx={{
                    color: "text.secondary",
                    padding: "0% 2%",
                  }}
                  value="experience"
                  label="EXPERIENCE"
                />
                <Tab
                  sx={{
                    color: "text.secondary",
                    padding: "0% 2%",
                  }}
                  value="contact"
                  label="CONTACT"
                />
              </Tabs>
            </Box>
          </div>
          <div className={classes.componentsDiv}>
            <TabPanel
              sx={{
                margin: "0px",
                padding: "0px",
              }}
              value="home"
            >
              <Home />
    
            </TabPanel>
            <TabPanel
              sx={{
                margin: "0px",
                padding: "0px",
              }}
              value="skills"
            >
              
              <Skills />
            </TabPanel>
            <TabPanel
              sx={{
                margin: "0px",
                padding: "0px",
              }}
              value="experience"
            >
              <Experience/>
            </TabPanel>
            <TabPanel
              sx={{
                margin: "0px",
                padding: "0px",
              }}
              value="contact"
            >
              <Contact/>
            </TabPanel>
          </div>
        </TabContext>
      </ThemeProvider>
    </div>
  );
};

export default Header;
